@media (min-width: 992.99px) {
    .banner {
        padding: 0 12px;

        .swiper-slide {
            border-radius: 20px;
            padding: 0 132px;
        }
 }

    .template-title {
            &.case-2 {
                text-align: left;
    
                &:before {
                    left: 0;
                    transform: none;
                }
            }
            &.case-3 {
                max-width: 626px;
                text-align: left;
       
                  &:before {
                     display: none;
                  }

            &.case-4 {
                margin-left: -68px;
                  }
           h2 { 
           &::after {
              content: "";
              background-color: $secondary-color;
              width: 85px;
              height: 2px;
              display: inline-block;
              margin: 0 20px 10px;
       
            }
         }
       }
    }


    #advantages {
        padding-top: 17px;
        width: calc(100% + 28px);
        margin: 0 -45px;
        .swiper-wrapper {
            flex-wrap: wrap;
            
           
        }

        .advantage {
            width: 100%;
            max-width: 25%;
            padding: 0 22px;

        }

    }
}



